
:root {
  --mainBlue: #140dda;
  --mainWhite: #fff;
  --grey: #d6d6d6;
}


body {
  background-color: #eff3ff;
}


h1, h2 {
  font-family: 'Archivo Black', serif;
}

h1 {
  color: #fff;
  color: var(--mainWhite);
  font-size: 300%;
  max-width: 500px;
  text-align: left;
}

p {
  margin: 0 0 15px;
}

img {
  width: 100%;
  height: auto;
}



button {
  background-color: var(--mainBlue);
  border: none;
  border-radius: 5px;
  box-shadow: 0 0 5px 0 grey;
  color: var(--mainWhite);
  cursor: pointer;
  padding: 10px 20px;
}

button:active {
  box-shadow: none;
}

button:disabled {
  background-color: var(--grey);
  box-shadow: none;
  color: var(--mainBlue);
  cursor: not-allowed;
}

/* HERO SECTION */

.hero-container {
  background-image: linear-gradient(25deg, #625df5, #140dda, #342df2);
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  width: 100%;
  z-index: 0;
}
.App-header {
  align-items: center;
  display: flex;
  padding-left: 5%;
  padding-top: 2%;
}

.logo {
  height: auto;
  width: 50px;
}

.App-header p {
  color: var(--mainWhite);
  font-weight: 700;
  margin-left: 15px;
}

.hero {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  height: 80vh;
  justify-content: space-between;
  overflow: hidden;
  padding: 2% 5% 0;
}

.hero-text {
  display: flex;
  flex-direction: column;
}

.hero-img {
  bottom: -3px;
  height: auto;
  max-width: 460px;
  position: absolute;
  right: 5%;
  transform: translateX(10%);
  width: 50%;
  z-index: -1;
}

/* TEE SECTION */
.tee-section {
  margin: 50px 0;
}

.tee-container {
  grid-gap: 20px;
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(2, 1fr);
  margin: 1% auto 2%;
  max-width: 85%;
  position: relative;
}

.customizer-header {
  color: var(--mainBlue);
  display: flex;
  font-size: 2em;
  justify-content: center;
  margin-top: 0;
  padding: 5px 0;
  text-align: left;
}

.mobile-visible {
  display: none;
}

/* color palette */
.palette-label {
  margin-bottom: 8px;
}

.color-palette {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  justify-content: flex-start;
  margin-bottom: 50px;
  max-width: 500px;
  width: 80%;
}

.tee-customizer-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
}

/* TEE SELECTION */
.tee-selection {
  align-items: center;
  background-color: var(--grey);
  border-radius: 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5% 0;
  width: 100%;
}

.tee-shirt-img {
  height: auto;
  margin: 0 auto;
  max-width: 500px;
  position: relative;
  width: 75%;
}
.label-overlay, .tee-shirt-img img {
  width: 100%;
  height: auto;
}

.label-overlay {
  left: 50%;
  max-width: 500px;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}


/* FORM */
.form-container {
  display: flex;
  width: 100%;
}

.form-box {
  transition: all 2s ease-in;
}

.section-left {
  align-items: center;
  background-color: var(--mainWhite);
  box-shadow: 0 0 5px 0 grey;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
  left: 50%;
  max-width: 550px;
  padding: 1% 2%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  z-index: 1;
}

.close-form {
  color: var(--mainBlue);
  cursor: pointer;
  font-size: 1.5em;
  padding: 5px 10px;
  position: absolute;
  right: 5%;
  top: 3%;
}

form {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 8%;
  width: 80%;
}


label {
  align-self: flex-start;
  margin-bottom: 5px;
}

input {
  background-color: initial;
  border: 1px solid var(--mainBlue);
  border-radius: 5px;
  box-sizing: border-box;
  height: 35px;
  margin-bottom: 20px;
  outline: none;
  padding: 0 10px;
  width: 100%;
}

.tiny {
  font-size: 13px;
}

.form-btn-mobile {
  display: none;
}

/* END OF FORM */

.footer {
  background-color: var(--mainBlue);
  color: #fff;
  color: var(--mainWhite);
  margin-top: 10px;
  padding: 15px 0;
  text-align: center;
  width: 100%;
}

/* THANK YOU PAGE */
.thank-you-page {
  align-items: center;
  background-image: linear-gradient(25deg, #625df5, #140dda, #342df2);
  box-sizing: border-box;
  color: #fff;
  color: var(--mainWhite);
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  padding: 0 25px;
  width: 100%;
}

.chi-logo {
  max-width: 400px;
}

.big {
  font-size: xx-large;
  font-weight: 700;
}

.ig-link {
  font-size: 1.3em;
  margin-top: 25px;
}

.ig-link a {
  color: var(--mainWhite);
  font-weight: 700;
}
/* RESPONSIVE CSS */

@media screen and (max-width: 768px) {

  h1 {
    font-size: 200%;
    text-align: center;
  }
  /* HERO SECTION */
  .hero-container {
    background-image: linear-gradient(25deg, #625df5e6, #140ddae6, #342df2);
    overflow: hidden;
    z-index: auto;
  }
  .hero {
    justify-content: center;
  }

  .hero-img {
    bottom: -3px;
    height: auto;
    left: 50%;
    transform: translateX(-50%);
    width: 200%;
    z-index: -1;
  }

  /* TEE SECTION */
  .tee-container {
    display: flex;
    flex-direction: column;
  }

  .customizer-header {
    font-size: 1.5em;
  }

  .mobile-visible {
    display: inline;
  }

  .color-palette {
    margin-bottom: 20px;
  }

  .section-left {
    height: 55%;
    padding: 10% 5%;
    width: 110%;
  }

  .fa-solid, .fas {
    font-size: .7em;
  }

  .tiny {
    font-size: 10px;
    margin-bottom: 0;
    margin-top: 15px;
  }

  .tee-selection {
    height: 100%;
  }

  .form-btn-desktop {
    display: none;
  }

  .form-btn-mobile {
    display: block;
  }
}

@media screen and (max-width: 570px) {
  .hero-img {
    right: 50%;
    transform: translateX(50%);
  }

}